import React from "react";
import MainMenu from "@webmens-ru/main-menu";
import Greetings from "../../components/Greetings";
import { MainWrap } from "./styles";
import { useGetTabsQuery, useSetTabsMutation } from "../../app/store/menu/api";

const Main = () => {
  // const {data, isSuccess, isLoading} = useGetTabsQuery("1");

  const data = [
    {id: 1, title: 'Главная', visible: 1, order: 1, menuId: 1, type: 'updatePage'},
    {id: 2, title: 'Админка', visible: 0, order: 2, menuId: 1, type: 'updatePage'}
  ]

  const [tabsMut] = useSetTabsMutation();
  const setTab = (tab) => {
    console.log(tab);
  };
  const tabsMutation = (tabs) => {
    tabsMut(
      tabs.map((tab) => ({
        id: tab.id,
        visible: tab.visible,
        order: tab.order,
      })),
    );
  };

  return (
    <MainWrap>
      <MainMenu tabs={data} setTab={setTab} tabsMutation={tabsMutation} />
      <Greetings />
    </MainWrap>
  )
}

export default Main