import React from "react";
import Main from "./pages/Main/index.jsx"

function App() {
  const switchPath = (path) => {
    console.log(path)
    switch (path) {
      case "main":
        return <Main />;
      default:
        return <Main />;
    }
  };

  if (process.env.NODE_ENV === "production") {
    try {
      return switchPath(window._PARAMS_.placementOptions.path);
    } catch (error) {}
  } else {
    const pathArr = window.location.pathname.replace("/", "").split("=");
    const pathObj = {};
    for (let i = 0; i < pathArr.length; i += 2) {
      pathObj[pathArr[i]] = pathArr[i + 1];
    }
    if (pathObj.hasOwnProperty("path")) {
      return switchPath(pathObj.path);
    }
  }

  return <Main />;
}

export default App;