import { createApi } from "@reduxjs/toolkit/query/react";
import baseQuery from "../../api/storeApi";

export const menuApi = createApi({
  reducerPath: "menu",
  tagTypes: ["Tabs"],
  baseQuery,
  endpoints: (build) => ({
    getTabs: build.query({
      query: (menuId) => ({url: `admin/ui/menu/menu-item/items?menuId=${menuId}`})
    }),
    setTabs: build.mutation({
      query: (tabs) => ({
        url: 'admin/ui/menu/menu-item-personal-settings/save-items',
        method: "POST",
        body: tabs
      })
    })
  }),
});

export const {
  useGetTabsQuery,
  useSetTabsMutation,
} = menuApi;